wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import '@dcloudio/vue-cli-plugin-uni/packages/uni-cloud/dist/index.js';import App from './App'
import store from './store/store.js'

import Mixin from './polyfill/mixins'
Vue.mixin(Mixin)


import Vue from 'vue'

// 封装弹框的方法
uni.$showMsg = function(title = '成功', icon = 'success', duration = 1500) {
  uni.showToast({
    title,
    duration,
    icon
  })
}

Vue.config.productionTip = false
App.mpType = 'app'

try {
  function isPromise(obj) {
    return (
      !!obj &&
      (typeof obj === "object" || typeof obj === "function") &&
      typeof obj.then === "function"
    );
  }

  // 统一 vue2 API Promise 化返回格式与 vue3 保持一致
  uni.addInterceptor({
    returnValue(res) {
      if (!isPromise(res)) {
        return res;
      }
      return new Promise((resolve, reject) => {
        res.then((res) => {
          if (res[0]) {
            reject(res[0]);
          } else {
            resolve(res[1]);
          }
        });
      });
    },
  });
} catch (error) { }

const app = new Vue({
  store,
  ...App
})
app.$mount()